<script>
import util from "../../../util/date";
import {
    getPermissionTreeTable,
    removePermission,
    editPermission,
    addPermission,
    getPermissionTree,
    getModuleAllList,
    getSystemListAll,
} from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";

export default {
    components: { Toolbar },
    data() {
        return {
            buttonList: [],
            currentRow: null,
            options: [],
            filters: {
                Name: "",
            },
            users: [],
            modules: [], //接口api列表
            systems: [],
            systemid: global.systemid,
            statusList: [
                { Name: "激活", value: true },
                { Name: "禁用", value: false },
            ],
            total: 0,
            page: 1,
            listLoading: false,
            sels: [], //列表选中列
            cascaderKey: 0,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                Name: [
                    {
                        required: true,
                        message: "请输入菜单名称",
                        trigger: "blur",
                    },
                ],
                Url: [
                    {
                        required: true,
                        message: "请输入路由地址",
                        trigger: "blur",
                    },
                ],
            },
            //编辑界面数据
            editForm: {
                Id: 0,
                ApiId: 0,
                ApiIds: [],
                SortNo: 0,
                PidArr: [],
                Name: "",
                Url: "",
                Icon: "",
                Func: "",
                Enabled: true,
                IsButton: false,
                IsHide: false,
            },

            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                SystemId: [
                    {
                        required: true,
                        message: "请选择系统类型",
                        trigger: "blur",
                    },
                ],
                Name: [
                    {
                        required: true,
                        message: "请输入菜单名称",
                        trigger: "blur",
                    },
                ],
                Url: [
                    {
                        required: true,
                        message: "请输入路由地址",
                        trigger: "blur",
                    },
                ],
            },
            //新增界面数据
            addForm: {
                PidArr: [],
                ApiId: 0,
                ApiIds: [],
                sortno: 0,
                Name: "",
                Url: "",
                Icon: "",
                Func: "",
                Enabled: true,
                IsButton: false,
                IsHide: false,
            },
            canEdit: false,
            canDelete: false,
            maps: new Map(),
            currpid: "",
        };
    },
    methods: {
        selectCurrentRow(val) {
            this.currentRow = val;
        },
        clkType() {
            this.addForm.IsButton = false;
            if (this.addForm.MenuType == "页面") {
                this.addForm.Url = "";
            } else if (this.addForm.MenuType == "目录") {
                this.addForm.Url = "-";
            } else if (this.addForm.MenuType == "按钮") {
                this.addForm.Url = " ";
                this.addForm.IsButton = true;
            }
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getPermissions();
        },
        load(tree, treeNode, resolve) {
            this.maps.set(tree.Id, { tree, treeNode, resolve });
            this.currnode = { tree, treeNode, resolve };
            let para = {
                systemid: this.systemid,
                parentid: tree.Id,
                key: this.filters.name,
            };
            getPermissionTreeTable(para).then((res) => {
                resolve(res.data);
            });
        },
        //获取列表
        getPermissions() {
            let para = {
                systemid: this.systemid,
                key: this.filters.name,
            };
            this.listLoading = true;
            getPermissionTreeTable(para).then((res) => {
                this.users = res.data;
                this.listLoading = false;
            });
        },
        //删除
        handleDel(index, r) {
            this.currpid = r.ParentId;
            let row = r ? r : this.currentRow;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.Id };
                    removePermission(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.listLoading = false;
                            return;
                        }
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        // this.getPermissions();

                        //重新加载当前节点父节点下的所有数据
                        this.reloadNode(this.currpid);
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        handleEdit(index, r) {
            this.currpid = r.ParentId;
            let row = r ? r : this.currentRow;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            let that = this;
            that.editLoading = true;

            this.editFormVisible = true;
            this.editForm = {};
            ++this.cascaderKey;
            this.options = [];

            let para = { pid: row.Id };
            getPermissionTree(para).then((res) => {
                this.options.push(res.data);
                that.editForm = Object.assign({}, row);
                that.editLoading = false;
            });
        },
        //显示新增界面
        handleAdd() {
            this.options = [];
            this.addFormVisible = true;
            this.addForm = {
                PidArr: [],
                Name: "",
                Url: "",
                sortno: 0,
                Enabled: true,
                Icon: "",
                IsButton: false,
                IsHide: false,
            };
            ++this.cascaderKey;
            let para = { pid: 0 };
            this.addForm.SystemId = this.systemid;
            getPermissionTree(para).then((res) => {
                this.options.push(res.data);
            });
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    //NProgress.start();
                    let para = Object.assign({}, this.editForm);
                    para.ParentId = para.PidArr.pop();
                    if (para.Id == para.ParentId) {
                        this.$message({
                            message: "警告，父节点不能是自己！",
                            type: "error",
                        });

                        this.editLoading = false;
                        return;
                    }

                    editPermission(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.editLoading = false;
                            return;
                        }
                        this.editLoading = false;
                        if (res.success) {
                            this.editLoading = false;
                            //NProgress.done();
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            // this.getPermissions();
                            //重新加载当前节点父节点下的所有数据
                            this.reloadNode(this.currpid);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    this.addLoading = true;
                    //NProgress.start();
                    let para = Object.assign({}, this.addForm);

                    para.ParentId = para.PidArr.pop();
                    addPermission(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.addLoading = false;
                            return;
                        }
                        this.addLoading = false;
                        if (res.success) {
                            this.addLoading = false;
                            //NProgress.done();
                            this.$message({
                                message: "添加成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            // this.getPermissions();
                            //重新加载当前节点父节点下的所有数据
                            this.reloadNode(para.ParentId);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                }
            });
        },
        reloadNode(pid) {
            if (pid == 0) {
                this.getPermissions();
            } else {
                let { tree, treeNode, resolve } = this.maps.get(pid);
                // this.$set(
                //     this.$refs.table.store.states.lazyTreeNodeMap,
                //     this.currpid,
                //     []
                // ); //将对应节点下的数据清空，从而实现数据的重新加载
                this.load(tree, treeNode, resolve);
            }
        },
        selsChange: function (sels) {
            this.sels = sels;
        },
        //批量删除
        batchRemove: function () {
            this.$message({
                message: "该功能未开放",
                type: "warning",
            });
        },
        getSystems() {
            getSystemListAll().then((res) => {
                if (res.success) {
                    this.systems = res.data;
                }
            });
        },
        systemClick(id) {
            this.systemid = id;
            this.getPermissions();
        },
    },
    mounted() {
        this.getPermissions();
        getModuleAllList().then((res) => {
            this.modules = res.data;
        });
        this.getSystems();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>

<style scoped>
.text {
    font-size: 14px;
    padding: 10px;
}

/* .item {
    margin-bottom: 12px;
} */

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}
.clearfix:after {
    clear: both;
}

.box-card {
    width: 100%;
}
.box-card .el-card__body {
    padding: 0 !important;
}
.box-card .item.active {
    background: #ebf5ff;
}

.box-card .item:hover {
    background: #ebf5ff;
}
</style>



